);\n  letter-spacing: var(--semantic-typography-hero\\/1-bold-letterSpacing);\n  font-weight: var(--semantic-typography-hero\\/1-bold-fontWeight);\n  color: var(--semantic-color-secondary-foreground-1-rest);\n  margin-bottom: 24px;\n  border-bottom: none !important;\n}\n\n.sbdocs-h2 {\n  font-size: var(--semantic-typography-title\\/1-bold-fontSize);\n  line-height: var(--semantic-typography-title\\/1-bold-lineHeight);\n  letter-spacing: var(--semantic-typography-title\\/1-bold-letterSpacing);\n  font-weight: var(--semantic-typography-title\\/1-bold-fontWeight);\n  color: var(--semantic-color-secondary-foreground-1-rest);\n  margin-top: 32px;\n  margin-bottom: 16px;\n  padding-bottom: 8px;\n  border-bottom: 1px solid var(--foundation-color-grey-light-90);\n}\n\n.sbdocs-h3 {\n  font-size: var(--semantic-typography-heading\\/1-bold-fontSize);\n  line-height: var(--semantic-typography-heading\\/1-bold-lineHeight);\n  letter-spacing: var(--semantic-typography-heading\\/1-bold-letterSpacing);\n  font-weight: var(--semantic-typography-heading\\/1-bold-fontWeight);\n  color: var(--semantic-color-secondary-foreground-1-rest);\n  margin-top: 24px;\n  margin-bottom: 12px;\n}\n\n.sbdocs-h4 {\n  font-size: var(--semantic-typography-heading\\/2-bold-fontSize);\n  line-height: var(--semantic-typography-heading\\/2-bold-lineHeight);\n  letter-spacing: var(--semantic-typography-heading\\/2-bold-letterSpacing);\n  font-weight: var(--semantic-typography-heading\\/2-bold-fontWeight);\n  color: var(--semantic-color-secondary-foreground-2-rest);\n  margin-top: 20px;\n  margin-bottom: 8px;\n}\n\n.sbdocs-p {\n  font-size: var(--semantic-typography-body\\/1-regular-fontSize);\n  line-height: var(--semantic-typography-body\\/1-regular-lineHeight);\n  letter-spacing: var(--semantic-typography-body\\/1-regular-letterSpacing);\n  font-weight: var(--semantic-typography-body\\/1-regular-fontWeight);\n  color: var(--semantic-color-secondary-foreground-2-rest);\n  margin-bottom: 16px;\n}\n\n/* 코드 블