n  padding: 8px 12px;\n}\n\n.sbdocs-table td {\n  border: 1px solid var(--foundation-color-grey-light-90);\n  padding: 8px 12px;\n}\n\n/* 컨트롤 패널 */\n.docblock-argstable {\n  border-radius: 4px !important;\n  border: 1px solid var(--foundation-color-grey-light-90) !important;\n  overflow: hidden !important;\n}\n\n.docblock-argstable-head,\n.docblock-argstable-headrow {\n  font-size: var(--semantic-typography-label\\/1-bold-fontSize);\n  line-height: var(--semantic-typography-label\\/1-bold-lineHeight);\n  letter-spacing: var(--semantic-typography-label\\/1-bold-letterSpacing);\n  font-weight: var(--semantic-typography-label\\/1-bold-fontWeight);\n  background-color: var(--foundation-color-grey-light-98) !important;\n}\n\n.docblock-argstable-body,\n.docblock-argstable-row {\n  font-size: var(--semantic-typography-body\\/2-regular-fontSize);\n  line-height: var(--semantic-typography-body\\/2-regular-lineHeight);\n  letter-spacing: var(--semantic-typography-body\\/2-regular-letterSpacing);\n  font-weight: var(--semantic-typography-body\\/2-regular-fontWeight);\n}\n\n/* 버튼 스타일 */\n.docblock-argstable-button,\n.sbdocs-button {\n  font-size: var(--semantic-typography-label\\/2-regular-fontSize);\n  line-height: var(--semantic-typography-label\\/2-regular-lineHeight);\n  letter-spacing: var(--semantic-typography-label\\/2-regular-letterSpacing);\n  font-weight: var(--semantic-typography-label\\/2-regular-fontWeight);\n  background-color: var(--foundation-color-grey-light-98) !important;\n  border: 1px solid var(--foundation-color-grey-light-90) !important;\n  border-radius: 2px !important;\n  color: var(--semantic-color-secondary-foreground-1-rest) !important;\n  transition: all 0.2s ease !important;\n  padding: 6px 12px !important;\n}\n\n.docblock-argstable-button:hover,\n.sbdocs-button:hover {\n  background-color: var(--foundation-color-grey-light-94) !important;\n  color: var(--semantic-color-primary-foreground-1-rest) !important;\n}\n\n/* 빈 컨트롤 영역 */\n.docblock-emptyblo